import React, { Component } from 'react';
import axios from "axios";
import Search from './Search';
import Filter from './Filter';
import Display from './Display';
import Header from './Header';
import '../style.css';

class App extends Component{
  constructor(props){
    super(props);
    this.state = {
      marks: [],
	  error: null,
	  isLoaded: false
    }
  }
  componentDidMount(){
	  axios.get(`../Marks.json`)
		.then(res => {
			console.log(res);
			const marks = res.data;
			this.setState({marks, isLoaded: true});
		})
		.catch(err => {
			this.setState({
				isLoaded: true,
				error: err
			});
			console.log(err);
		});
      
  }
  render(){
	  const {marks, isLoaded, error} = this.state;
	  if(error){
		  return <div>Error: {error.message}</div>
	  }
	  else if(!isLoaded){
		  return <div>Loading...</div>
	  }
	  else{
		  return(
			  <div>
				<Header />
				<div className="main">
					<Search marks={this.state.marks}/>
					<Filter />
					<Display />
				</div>
				<button> Submit </button>
			  </div>
			)
	  }
    
  }
}

export default App;