import React, { Component } from 'react';
import PropTypes from 'prop-types';
import '../style.css';

class TypeAhead extends Component{
	static propTypes = {
		options: PropTypes.instanceOf(Array).isRequired
	}
	state = {
		userInput: '',
		filteredOptions: [],
		activeOption: 0,
		showList: false
	}
	constructor(props){
		super(props);
	}
	onChange = (e) => {
		console.log('onChange');
		const {options} = this.props;
		const userInput = e.currentTarget.value;
		
		const filteredOptions = options.filter(
			(name) => name.toLowerCase().indexOf(userInput.toLowerCase()) > -1
		);
		this.setState({
			userInput,
			filteredOptions,
			activeOption: 0,
			showList: true
		})
	}
	onKeyDown = (e) => {
		const { activeOption, filteredOptions} = this.state;
		if(e.keyCode === 13){
			this.setState({
				activeOption: 0,
				userInput: filteredOptions[activeOption],
				showList: false
			});
		}
		else if(e.keyCode === 38){
			if(activeOption === 0){
				return;
			}
			this.setState({activeOption: activeOption - 1})
		}
		else if(e.keyCode === 40){
			if(activeOption === filteredOptions.length - 1){
				return;
			}
			this.setState({activeOption: activeOption + 1});
		}
	}
	onClick = (e) => {
		this.setState({
		  activeOption: 0,
		  filteredOptions: [],
		  showList: false,
		  userInput: e.currentTarget.innerText
		});
	};
	render(){
		const { onChange, onClick, onKeyDown, state: {  userInput, filteredOptions, activeOption, showList }} = this;
		let optionList;
		if(showList && userInput){
			if(filteredOptions.length){
				optionList = (
					<div className="dropdown-menu">
					{filteredOptions.map((name, index) => {
						/*let className;
						if(index === activeOption){
							className = "option-active";
						}*/
						return(
							<a className="dropdown-item" key={name} onClick={onClick}>
								{name}
							</a>
						)
					})}
					</div>
				)
			}
		}
		return(
			<React.Fragment>
				<div className="Search">
					<input 
						type="text"
						className="search-box"
						onChange={onChange}
						onKeyDown={onKeyDown}
						value={userInput}
					/>
					
				</div>
				{optionList}
			</React.Fragment>
		)
	}
}

export default TypeAhead;